.full-banner {
    display: flex;
    margin: 0 20px;
}

.half-banner {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;

    &--right {
        order: 2;
    }

    img {
        width: 100%;
        height: 300px;
        object-fit: cover;
    }
}
